html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 400;
  background-color: #FFFFFF;
  padding: 40px 20px;
  margin: 0;
}
@media screen and (min-width: 960px) {
  body {
    padding: 60px 140px;
  }
}

.header__logo {
  text-align: center;
  margin-bottom: 20px;
}
.header__image {
  max-width: 100%;
}
@media screen and (min-width: 768px) {
  .header__image {
    margin-top: 20px;
  }
}
.header__menu {
  display: flex;
  list-style: none;
  padding: 0;
  justify-content: space-between;
  gap: 20px;
}
.header__link {
  color: #F03000;
  font-size: 18px;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
.header__link:hover, .header__link:focus {
  background-color: #F03000;
  color: white;
}
@media screen and (min-width: 768px) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
}

.product {
  display: flex;
  min-height: 240px;
  border: 2px solid grey;
  flex-direction: column;
  justify-content: space-between;
}
.product__price {
  color: white;
  background-color: #F03000;
  font-size: 18px;
  padding: 10px;
  display: block;
  max-width: 110px;
  margin: 20px;
}
.product__header {
  font-size: 28px;
  font-weight: 600;
  margin-right: 30px;
  margin-left: 20px;
  color: #FFFFFF;
}
.product__header--small {
  font-size: 20px;
}
.product__content {
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 400;
  margin-left: 20px;
  color: #FFFFFF;
}
.product--img-palmy {
  background-image: url(../images/palmy.png);
  background-size: cover;
}
.product--img-jezero {
  background-image: url(../images/jezero2.png);
  background-size: cover;
}
.product--img-nakole {
  background-image: url(../images/nakole.png);
  background-size: cover;
}
.product--img-lodicka {
  background-image: url(../images/lodicka.png);
  background-size: cover;
}
.product--img-podkova {
  background-image: url(../images/podkova.png);
  background-size: cover;
}
.product--img-zatoka {
  background-image: url(../images/zatoka.png);
  background-size: cover;
}
@media screen and (min-width: 768px) {
  .product--main {
    grid-column: span 2;
  }
}
@media screen and (min-width: 960px) {
  .product--main {
    grid-column: 1;
    grid-row: span 2;
  }
}

.products-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin-bottom: 20px;
}
@media screen and (min-width: 768px) {
  .products-grid {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 380px 250px;
    gap: 20px;
  }
}
@media screen and (min-width: 960px) {
  .products-grid {
    grid-template-columns: 1fr 374px;
    grid-template-rows: auto;
    gap: 20px;
  }
}

.icon {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: #F0F0F0;
}
.icon__image {
  flex: 0 0 15%;
  margin: 20px 0 0 30px;
}
@media screen and (min-width: 768px) {
  .icon__image {
    margin: 20px 0 0 0;
  }
}
.icon__image img {
  max-width: 100%;
}
.icon_content {
  flex: 0 1 75%;
}
.icon__content h1 {
  font-size: 18px;
  color: #F03000;
  margin: 20px;
}
.icon__content p {
  margin: 0 20px 20px 20px;
}
@media screen and (min-width: 768px) {
  .icon {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

.icons-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
@media screen and (min-width: 768px) {
  .icons-grid {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 188px 188px;
    gap: 20px;
    margin-top: 30px;
  }
}
@media screen and (min-width: 960px) {
  .icons-grid {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: auto;
    gap: 20px;
    margin-top: 30px;
  }
}

h2 {
  margin: 40px 0 20px 0;
}

.news {
  background-color: #F0F0F0;
  text-align: left;
  border: 1px solid #F0F0F0;
}
.news__date {
  color: #888888;
  margin: 20px 20px 10px 20px;
}
.news__header {
  color: #F03000;
  margin: 0 20px 10px 20px;
}
.news__content {
  color: #000000;
  margin: 0 20px 20px 20px;
}
.news-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
@media screen and (min-width: 960px) {
  .news-grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.footer {
  background-color: black;
  color: #FFFFFF;
  padding: 20px;
  text-align: center;
  margin-top: 40px;
}
.footer__content {
  margin: 0;
  padding: 0;
}